.image__wrapper[data-v-d708cdba] {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-radius: 4px;
}
.image__wrapper.loaded .image__item[data-v-d708cdba] {
    visibility: visible;
    opacity: 1;
    border: 0;
}
.image__wrapper.loaded .image__spinner[data-v-d708cdba] {
    display: none;
    width: 100%;
}
.image__item[data-v-d708cdba] {
  width: 100%;
  border-radius: 4px;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  opacity: 0;
  visibility: hidden;
}

.ripple{
	display: inline-block;
	position:absolute;
	width:64px;
	height:64px;
}
.ripple__circle{
	position:absolute;
	border:4px solid rgb(96,208,112);
	opacity:1;
	border-radius:50%;
	-webkit-animation:ripple 1s cubic-bezier(0,.2,.8,1) infinite;
	        animation:ripple 1s cubic-bezier(0,.2,.8,1) infinite;
}
.ripple__inner-circle{
	-webkit-animation-delay:-.5s;
	        animation-delay:-.5s;
}
@-webkit-keyframes ripple{
0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
}
100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
}
}
@keyframes ripple{
0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
}
100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
}
}
